	// 单个设备添加工作计划
	addWorkPlan() {
			if(this.cur_interval === ''){
					this.message.warning('请输入心跳间隔')
					return false
			}
			const condition = {
					id:this.singleDevice.id,
					cur_interval: this.cur_interval
			}
			this.deviceService.heart(condition).subscribe((res)=>{
					if(res['code'] === 0){
							this.message.success('设置成功')
							this.isOperate = false;
					}
			})
	}

