	 //构建自定义信息窗体
	 createInfoWindow(title, content,that) {
				console.log(that)
				var info = document.createElement("div");
				info.className = "info";

				//可以通过下面的方式修改自定义窗体的宽高
				//info.style.width = "400px";
				// 定义顶部标题
				var top = document.createElement("div");
				var titleD = document.createElement("span");
				var closeX = document.createElement("img");
				top.className = "info-top";
				titleD.innerHTML = title;
				closeX.src = "https://webapi.amap.com/images/close2.gif";
				closeX.onclick = this.closeInfoWindow.bind(this);

				top.appendChild(titleD);
				top.appendChild(closeX);
				info.appendChild(top);

				// 定义中部内容
				var middle = document.createElement("div");
				middle.className = "info-middle";
				middle.style.backgroundColor = 'white';
				middle.innerHTML = content;
				info.appendChild(middle);

				// 定义底部内容
				var bottom = document.createElement("div");
				bottom.className = "info-bottom";
				bottom.style.position = 'relative';
				bottom.style.top = '0px';
				bottom.style.margin = '0 auto';
				var sharp = document.createElement("img");
				sharp.src = "https://webapi.amap.com/images/sharp.png";
				bottom.appendChild(sharp);
				info.appendChild(bottom);
				return info;
		}
		// 关闭信息窗体
		closeInfoWindow(that) {
				console.log(this)
				this.map.clearInfoWindow();
		}
		chart;
	    renderChart(){
	        const heart = this.singleDevice.heart;
	        let arr = []
	        for(var i = 0; i < heart.length;i++){
	            const obj = {
	                date:heart[i].created,
	                '光照度':heart[i].temperature,
	                '温度':heart[i].beam
	            }
	            arr.push(obj)
	        }
	        this.chart = new G2.Chart({
	          container: 'chartLock',
	          forceFit: true,
	          height: 500,
	          padding:'auto'
	        });
	        this.chart.source(arr, {
	          beam: {
	            min: 0,
	            tickInterval: 50,
	            alias: '光照度'
	          },
	          temperature: {
	            // min: 5,
	            tickInterval: 5,
	            alias: '温度'
	          }
	        });
	        // 左侧 Y 轴，即光照轴
	        this.chart.axis('beam', {
	          label: {
	            formatter: val => {
	              return val + ' mm'; // 格式化坐标轴显示
	            },
	            textStyle: {
	              fill: '#95ceff'
	            }
	          },
	          line: null,
	          tickLine: null
	        });
	        // 右侧第一个 Y 轴，即温度轴
	        this.chart.axis('temperature', {
	          line: null,
	          tickLine: null,
	          label: {
	            formatter: val => {
	              return val + ' °C'; // 格式化坐标轴显示
	            },
	            textStyle: {
	              fill: '#90ed7d'
	            }
	          }
	        });
	        this.chart.legend({
	          position: 'top'
	        });
	        this.chart.tooltip({
	          crosshairs: {
	            type: 'line'
	          }
	        });
	        this.chart.line().position('date*光照度').color('blue').size(2); // 光照
	        this.chart.line().position('date*温度').color('red').size(2).shape('smooth'); // 温度
	        // this.chart.point().position('date*temperature').color('#90ed7d').shape('diamond');
	        this.chart.render();
	    }
